/* eslint-disable no-underscore-dangle */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL-UI
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

const sales = [
  {
    value: '블로그 건바이건',
  },
  {
    value: '블로그 월보장',
  },
  {
    value: '연관 검색어',
  },
  {
    value: '자동 완성',
  },
  {
    value: '인스타그램',
  },
  {
    value: '쇼핑 상위 노출',
  },
  {
    value: '플레이스',
  },
  {
    value: '마케팅 친구 자료',
  },
];

export default class DialogSaleEditComponent extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    data: PropTypes.objectOf(String).isRequired,
    handleChange: PropTypes.func.isRequired,
    checkChange: PropTypes.func.isRequired,
    createSales: PropTypes.func.isRequired,
    editSales: PropTypes.func.isRequired,
  };

  render() {
    const {
      open,
      onClose,
      data,
      handleChange,
      checkChange,
      createSales,
      editSales,
    } = this.props;
    return (
      <div>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">
            {data._id ? '판매 상품 편집' : '판매 상품 추가'}
          </DialogTitle>

          <DialogContent>
            <TextField
              margin="dense"
              id="goods"
              select
              label="판매 상품"
              onChange={handleChange('goods')}
              value={data.goods ? data.goods : ''}
              fullWidth
              variant="outlined"
            >
              {sales.map(option => (
                <MenuItem key={option.value} value={option.value}>
                  {option.value}
                </MenuItem>
              ))}
            </TextField>
            <TextField
              margin="dense"
              id="company"
              label="업체명"
              onChange={handleChange('company')}
              defaultValue={data.company ? data.company : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="name"
              label="이름"
              onChange={handleChange('name')}
              defaultValue={data.name ? data.name : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="contact"
              label="연락처"
              onChange={handleChange('contact')}
              defaultValue={data.contact ? data.contact : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="price"
              label="상품 금액"
              onChange={handleChange('price')}
              defaultValue={data.price ? data.price : null}
              type="number"
              inputProps={{ min: 0, step: 1000 }}
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="spend"
              label="지출 금액"
              onChange={handleChange('spend')}
              defaultValue={data.spend ? data.spend : null}
              type="number"
              inputProps={{ min: 0, step: 1000 }}
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="homepage"
              label="광고주 홈페이지"
              onChange={handleChange('homepage')}
              defaultValue={data.homepage ? data.homepage : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              multiline
              rows="5"
              id="description"
              label="상품 설명"
              onChange={handleChange('description')}
              defaultValue={data.description ? data.description : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <FormControlLabel
              control={
                // eslint-disable-next-line react/jsx-wrap-multilines
                <Checkbox
                  checked={data.deposit ? data.deposit : false}
                  onChange={checkChange('deposit')}
                  value="deposit"
                />
              }
              label="입금 여부"
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={data._id ? () => editSales() : () => createSales()}
            >
              확인
            </Button>
            <Button onClick={onClose}>취소</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
