import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL-UI
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

export default class DialogSaleEditComponent extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    data: PropTypes.objectOf(String).isRequired,
    handleChange: PropTypes.func.isRequired,
    checkChange: PropTypes.func.isRequired,
    createUsers: PropTypes.func.isRequired,
    editUsers: PropTypes.func.isRequired,
  };

  render() {
    const {
      open,
      onClose,
      data,
      handleChange,
      checkChange,
      createUsers,
      editUsers,
    } = this.props;
    return (
      <div>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">
            {data.goods ? '추가 지출 편집' : '추가 지출 추가'}
          </DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              id="userID"
              label="유저 아이디"
              onChange={handleChange('userID')}
              defaultValue={data.userID ? data.userID : ''}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="userPW"
              label="유저 비밀번호"
              onChange={handleChange('userPW')}
              defaultValue={data.userPW ? data.userPW : ''}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="userAKA"
              label="유저 이름"
              onChange={handleChange('userAKA')}
              defaultValue={data.userAKA ? data.userAKA : ''}
              type="text"
              fullWidth
              variant="outlined"
            />
            <FormControlLabel
              control={
                // eslint-disable-next-line react/jsx-wrap-multilines
                <Checkbox
                  checked={data.admin ? data.admin : false}
                  onChange={checkChange('admin')}
                  value="deposit"
                />
              }
              label="어드민"
            />
          </DialogContent>
          <DialogActions>
            <Button
              // eslint-disable-next-line no-underscore-dangle
              onClick={data._id ? () => editUsers() : () => createUsers()}
            >
              확인
            </Button>
            <Button onClick={onClose}>취소</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
