import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';

// MATERIAL
import Grid from '@material-ui/core/Grid';

// COMPONENT
import OverviewComponent from '../components/OverviewComponent';

// TODO: 만들어야함
// const view = [
//   { title: '담당자 월 매출', value: 'userSales' },
//   { title: '담당자 누적 매출', value: 'userAllSales' },
//   { title: '월 판매 건수', value: 'userAllSales' },
//   { title: '전체 매출', value: 'allSales' },
//   { title: '전체 지출', value: 'allSpends' },
// ];

class OverviewContainer extends PureComponent {
  static propTypes = {
    admin: PropTypes.bool.isRequired,
    rows: PropTypes.objectOf(Array).isRequired,
    sales: PropTypes.arrayOf(String).isRequired,
    spends: PropTypes.arrayOf(String).isRequired,
    allRows: PropTypes.arrayOf(String).isRequired,
    allSales: PropTypes.arrayOf(String).isRequired,
    allSpends: PropTypes.arrayOf(String).isRequired,
  };

  sumSale = arr => {
    const results = arr.filter(sale => sale.deposit);
    let sum = 0;
    for (const i in results) {
      sum += results[i].price - results[i].spend;
    }
    return sum;
  };

  sumSpend = arr => {
    let sum = 0;
    for (const i in arr) {
      sum += arr[i].price;
    }
    return sum;
  };

  render() {
    const {
      admin,
      sales,
      spends,
      rows,
      allSales,
      allSpends,
      allRows,
    } = this.props;
    const { sumSale, sumSpend } = this;
    return (
      <>
        {admin ? (
          <Grid container spacing={16}>
            <OverviewComponent title="월 매출">
              {sumSale(allSales)} 원
            </OverviewComponent>
            <OverviewComponent title="월 지출">
              {sumSpend(allSpends)} 원
            </OverviewComponent>
            <OverviewComponent title="월 수익">
              {sumSale(allSales) - sumSpend(allSpends)} 원
            </OverviewComponent>
            <OverviewComponent title="누적 수익">
              {sumSale(allRows.sales) - sumSpend(allRows.spends)} 원
            </OverviewComponent>
          </Grid>
        ) : (
          <Grid container spacing={16}>
            <OverviewComponent title="월 매출">
              {sumSale(sales)} 원
            </OverviewComponent>
            <OverviewComponent title="월 지출">
              {sumSpend(spends)} 원
            </OverviewComponent>
            <OverviewComponent title="월 수익">
              {sumSale(sales) - sumSpend(spends)} 원
            </OverviewComponent>
            <OverviewComponent title="누적 수익">
              {sumSale(rows.sales) - sumSpend(rows.spends)} 원
            </OverviewComponent>
          </Grid>
        )}
      </>
    );
  }
}

const mapStateToProps = state => ({
  admin: state.users.userInfo.admin,
  rows: state.table.value,
  allRows: state.admin.value,
});

export default connect(
  mapStateToProps,
  null,
)(OverviewContainer);
