/* eslint-disable no-shadow */
/* eslint-disable no-underscore-dangle */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// MATERIAL
import Fab from '@material-ui/core/Fab';
import Add from '@material-ui/icons/Add';

// ACTION
import * as adminActions from '../store/modules/admin';

// CONTAINER
import DialogUserEditComponent from '../components/DialogUserEditComponent';

// COMPONENT
import DialogDeleteComponent from '../components/DialogDeleteComponent';
import TableComponent from '../components/TableComponent';

const Columns = [
  { title: '유저 아이디', field: 'userID' },
  { title: '유저 비밀번호', field: 'userPW' },
  { title: '유저 이름', field: 'userAKA' },
  { title: '담당자 월 매출', field: 'month' },
  { title: '담당자 누적 매출', field: 'all' },
  {
    title: '어드민',
    field: 'admin',
    lookup: { true: '어드민', false: '' },
  },
];

class TableContainer extends PureComponent {
  static propTypes = {
    users: PropTypes.arrayOf(String).isRequired,
    AdminActions: PropTypes.objectOf(Function).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      openEdit: false,
      openDelete: false,
      data: {},
    };
  }

  // static getDerivedStateFromProps(nextProps, prevState) {
  //   if (nextProps.data._id !== prevState.data._id) {
  //     return { data: nextProps.data };
  //   }
  //   return null;
  // }

  componentDidMount() {
    const { AdminActions } = this.props;
    AdminActions.fatchUsers();
  }

  closeEdit = () => {
    this.setState({ openEdit: false });
  };

  closeDelete = () => {
    this.setState({ openDelete: false });
  };

  handleChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.value,
      },
    });
  };

  checkChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.checked,
      },
    });
  };

  createUsers = () => {
    const { AdminActions } = this.props;
    const { closeEdit } = this;
    const { data } = this.state;
    AdminActions.createUsers(data);
    AdminActions.fatchUsers();
    closeEdit();
  };

  editUsers = () => {
    const { AdminActions } = this.props;
    const { closeEdit } = this;
    const { data } = this.state;
    AdminActions.editUsers(data);
    AdminActions.fatchUsers();
    closeEdit();
  };

  render() {
    const { users } = this.props;
    const { openEdit, openDelete, data } = this.state;
    const {
      closeEdit,
      closeDelete,
      handleChange,
      checkChange,
      createUsers,
      editUsers,
    } = this;
    return (
      <>
        <DialogUserEditComponent
          open={openEdit}
          onClose={closeEdit}
          data={data}
          handleChange={handleChange}
          checkChange={checkChange}
          createUsers={createUsers}
          editUsers={editUsers}
        />
        <DialogDeleteComponent
          open={openDelete}
          onClose={closeDelete}
          data={data}
          type="admin"
        />
        <Fab
          size="small"
          color="primary"
          aria-label="Add"
          onClick={() =>
            this.setState({
              openEdit: true,
              data: {},
            })
          }
        >
          <Add />
        </Fab>
        <TableComponent
          columns={Columns}
          data={users}
          type="admin"
          actions={[
            {
              icon: 'edit',
              tooltip: '편집',
              onClick: (event, rowData) => {
                this.setState({
                  openEdit: true,
                  data: rowData,
                });
              },
            },
            {
              icon: 'delete',
              tooltip: '삭제',
              onClick: (event, rowData) => {
                this.setState({
                  openDelete: true,
                  data: rowData,
                });
              },
            },
          ]}
        />
      </>
    );
  }
}

const mapStateToProps = state => ({
  users: state.admin.users,
});

const mapDispatchToProps = dispatch => ({
  AdminActions: bindActionCreators(adminActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(TableContainer);
