import React, { PureComponent } from 'react';
import { NavLink } from 'react-router-dom';
import PropTypes from 'prop-types';

// MATERIAL-UI
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { withStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import {
  AccountCircle,
  AssignmentInd,
  Assessment,
  MonetizationOn,
  Menu,
  Home,
} from '@material-ui/icons';
import Logo from './logo.png';

const drawerWidth = 240;

const styles = ({ breakpoints, spacing, mixins, transitions }) => ({
  appBar: {
    marginLeft: spacing.unit * 7,
    transition: transitions.create(['width', 'margin'], {
      duration: transitions.duration.leavingScreen,
      easing: transitions.easing.sharp,
    }),
    width: `calc(100% - ${spacing.unit * 7}px)`,
  },
  appBarShift: {
    marginLeft: drawerWidth,
    transition: transitions.create(['width', 'margin'], {
      duration: transitions.duration.enteringScreen,
      easing: transitions.easing.sharp,
    }),
    width: `calc(100% - ${drawerWidth}px)`,
  },
  button: {
    whiteSpace: 'nowrap',
  },
  contents: {
    flexGrow: 1,
    padding: spacing.unit * 3,
    [breakpoints.down('xs')]: {
      marginLeft: spacing.unit * 7,
      padding: spacing.unit,
    },
  },
  drawer: {
    flexShrink: 0,
    overflowX: 'hidden',
    transition: transitions.create(['width'], {
      duration: transitions.duration.leavingScreen,
      easing: transitions.easing.sharp,
    }),
    whiteSpace: 'nowrap',
    width: spacing.unit * 7 + 1,
    [breakpoints.down('xs')]: {
      position: 'fixed',
      zIndex: 1,
    },
  },
  drawerShift: {
    overflowX: 'hidden',
    transition: transitions.create(['width'], {
      duration: transitions.duration.enteringScreen,
      easing: transitions.easing.sharp,
    }),
    whiteSpace: 'nowrap',
    width: drawerWidth,
    [breakpoints.down('xs')]: {
      position: 'fixed',
      zIndex: 1,
    },
  },
  link: {
    textDecoration: 'none',
  },
  linktext: {
    textAlign: 'center',
  },
  logo: {
    height: 40,
    marginLeft: 47,
  },
  root: {
    display: 'flex',
  },
  toolbar: {
    alignItems: 'center',
    display: 'flex',
    justifyContent: 'flex-start',
    ...mixins.toolbar,
  },
});

class LayoutComponent extends PureComponent {
  static propTypes = {
    classes: PropTypes.objectOf(String).isRequired,
    // eslint-disable-next-line react/forbid-prop-types
    children: PropTypes.any.isRequired,
    userInfo: PropTypes.objectOf(String).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      open: false,
    };
  }

  handleDrawerToggle = () => {
    const { open } = this.state;
    this.setState({ open: !open });
  };

  render() {
    const { open } = this.state;
    const { classes, children, userInfo } = this.props;
    return (
      <div className={classes.root}>
        {/* 상단 앱바 */}
        <AppBar
          position="fixed"
          className={open ? classes.appBarShift : classes.appBar}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="Toggle drawer"
              onClick={this.handleDrawerToggle}
            >
              <Menu />
            </IconButton>
            <form action="/api/users/logout" method="post">
              <Button type="submit" className={classes.button}>
                로그아웃
              </Button>
            </form>
          </Toolbar>
        </AppBar>
        {/* 네비게이션 */}
        <Drawer
          variant="permanent"
          className={open ? classes.drawerShift : classes.drawer}
          classes={{ paper: open ? classes.drawerShift : classes.drawer }}
        >
          {/* 브랜드 */}
          <NavLink to="/" className={classes.link}>
            <div className={classes.toolbar}>
              <ListItem>
                <ListItemIcon>
                  <Home />
                </ListItemIcon>
                <img src={Logo} className={classes.logo} alt="로고" />
              </ListItem>
            </div>
          </NavLink>
          <Divider />
          {/* 유저 정보 */}
          <List component="nav">
            <ListItem>
              <ListItemIcon>
                <AccountCircle />
              </ListItemIcon>
              <ListItemText
                primary={userInfo.userAKA}
                className={classes.linktext}
              />
            </ListItem>
          </List>
          <Divider />
          <List component="nav">
            {/* 매출 관리 */}
            <NavLink to="/table/sales" className={classes.link}>
              <ListItem button>
                <ListItemIcon>
                  <Assessment />
                </ListItemIcon>
                <ListItemText
                  primary="매출 관리"
                  className={classes.linktext}
                />
              </ListItem>
            </NavLink>
            {/* 추가 지출 */}
            <NavLink to="/table/spends" className={classes.link}>
              <ListItem button>
                <ListItemIcon>
                  <MonetizationOn />
                </ListItemIcon>
                <ListItemText
                  primary="추가 지출"
                  className={classes.linktext}
                />
              </ListItem>
            </NavLink>
          </List>
          <Divider />
          {userInfo.admin ? (
            <List component="nav">
              {/* 유저 관리 */}
              <NavLink to="/admin/users" className={classes.link}>
                <ListItem button>
                  <ListItemIcon>
                    <AssignmentInd />
                  </ListItemIcon>
                  <ListItemText
                    primary="유저 관리"
                    className={classes.linktext}
                  />
                </ListItem>
              </NavLink>
            </List>
          ) : null}
        </Drawer>
        {/* 콘텐츠 */}
        <main className={classes.contents}>
          <div className={classes.toolbar} />
          {children}
        </main>
      </div>
    );
  }
}

export default withStyles(styles)(LayoutComponent);
