import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Route, withRouter } from 'react-router-dom';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as userActions from '../store/modules/users';

// COMPONENT
import LoginComponent from '../components/LoginComponent';
import LayoutComponent from '../components/LayoutComponent';

// CONTAINER
import TableContainer from './TableContainer';
import AdminContainer from './AdminContainer';

class LayoutContainer extends PureComponent {
  static propTypes = {
    userInfo: PropTypes.objectOf(String).isRequired,
    UserActions: PropTypes.objectOf(Function).isRequired,
  };

  componentDidMount() {
    const { UserActions } = this.props;
    UserActions.fatchUser();
  }

  render() {
    const { userInfo } = this.props;
    return (
      <>
        {userInfo.userID ? (
          <LayoutComponent userInfo={userInfo}>
            <Route path="/table/:type" component={TableContainer} />
            <Route path="/admin/users" component={AdminContainer} />
          </LayoutComponent>
        ) : (
          <LoginComponent />
        )}
      </>
    );
  }
}

const mapStateToProps = state => ({
  userInfo: state.users.userInfo,
});

const mapDispatchToProps = dispatch => ({
  UserActions: bindActionCreators(userActions, dispatch),
});

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(LayoutContainer),
);
