import { createAction, handleActions } from 'redux-actions';
import { applyPenders } from 'redux-pender';
import axios from 'axios';
import produce from 'immer';

const apiUri = '/api/admin';

const CREATE_USERS = 'admin/CREATE_USERS';
const FATCH_USERS = 'admin/FATCH_USERS';
const FATCH_ALL = 'admin/FATCH_ALL';
const EDIT_USERS = 'admin/EDIT_USERS';
const DELETE_USERS = 'admin/DELETE_USERS';

const postCreateUsers = data => {
  return axios.post(`${apiUri}/create`, data);
};

const getFatchUsers = () => {
  return axios.get(`${apiUri}/fatch`);
};

const getFatchAll = () => {
  return axios.get(`${apiUri}/fatchall`);
};

const postEditUsers = data => {
  return axios.post(`${apiUri}/edit`, data);
};

const postDeleteUsers = data => {
  return axios.post(`${apiUri}/delete`, data);
};

export const createUsers = createAction(CREATE_USERS, postCreateUsers);
export const fatchUsers = createAction(FATCH_USERS, getFatchUsers);
export const fatchAll = createAction(FATCH_ALL, getFatchAll);
export const editUsers = createAction(EDIT_USERS, postEditUsers);
export const deleteUsers = createAction(DELETE_USERS, postDeleteUsers);

const initialState = {
  users: [],
  value: {
    sales: [],
    spends: [],
  },
};

const reducer = handleActions({}, initialState);

export default applyPenders(reducer, [
  {
    type: FATCH_USERS,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        // eslint-disable-next-line no-param-reassign
        draft.users = action.payload.data;
      });
    },
  },
  {
    type: FATCH_ALL,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        // eslint-disable-next-line no-param-reassign
        draft.value = action.payload.data;
      });
    },
  },
]);
