/* eslint-disable no-alert */
/* eslint-disable no-shadow */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// MATERIAL
import Fab from '@material-ui/core/Fab';
import Add from '@material-ui/icons/Add';

// ACTION
import * as adminActions from '../store/modules/admin';
import * as tableActions from '../store/modules/table';

// CONTAINER
import DialogEditContainer from './DialogEditContainer';
import DatePickerContainer from './DatePickerContainer';
import OverviewContainer from './OverviewContainer';

// COMPONENT
import DialogDeleteComponent from '../components/DialogDeleteComponent';
import TableComponent from '../components/TableComponent';

const salesColumns = [
  { title: '판매 상품', field: 'goods' },
  { title: '업체명', field: 'company' },
  { title: '이름', field: 'name' },
  { title: '연락처', field: 'contact' },
  { title: '상품 설명', field: 'description' },
  { title: '상품 금액', field: 'price' },
  { title: '지출 금액', field: 'spend' },
  {
    title: '마진 금액',
    field: 'margin',
    render: rowData => {
      return rowData.price - rowData.spend;
    },
  },
  {
    title: '입금 여부',
    field: 'deposit',
    lookup: { true: '입금', false: '미입금' },
    cellStyle: data => {
      if (data === '입금') {
        return {
          color: '#66bb6a',
        };
      }
      if (data === '미입금') {
        return {
          color: '#ef5350',
        };
      }
      return null;
    },
  },
  { title: '광고주 홈페이지', field: 'homepage' },
  {
    title: '판매 날짜',
    field: 'date',
    render: rowData => {
      return moment(rowData.date).format('YYYY/MM/DD');
    },
  },
];

const spendsColumns = [
  { title: '지출 상품', field: 'goods' },
  { title: '지출 내용', field: 'name' },
  { title: '지출 금액', field: 'price' },
  {
    title: '지출 날짜',
    field: 'date',
    render: rowData => {
      return moment(rowData.date).format('YYYY/MM/DD');
    },
  },
];

class TableContainer extends PureComponent {
  static propTypes = {
    match: PropTypes.objectOf(String).isRequired,
    date: PropTypes.string.isRequired,
    admin: PropTypes.string.isRequired,
    TableActions: PropTypes.objectOf(Function).isRequired,
    rows: PropTypes.objectOf(Array).isRequired,
    AdminActions: PropTypes.objectOf(Function).isRequired,
    allRows: PropTypes.objectOf(Array).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      openEdit: false,
      editData: {},
      openDelete: false,
      deleteData: {},
    };
  }

  componentDidMount() {
    const { AdminActions, TableActions, admin } = this.props;
    TableActions.fatchRows('/api/sales');
    TableActions.fatchRows('/api/spends');
    if (admin) {
      AdminActions.fatchAll();
    }
  }

  closeEdit = () => {
    this.setState({ openEdit: false });
  };

  closeDelete = () => {
    this.setState({ openDelete: false });
  };

  dataFatch = (type, date, data) => {
    const Date = moment(date, 'YYYY/MM');
    const DateStart = moment(Date).startOf('month');
    const DateEnd = moment(Date).endOf('month');
    const results = data[type].filter(
      result =>
        moment(result.date) > DateStart && moment(result.date) < DateEnd,
    );
    return results;
  };

  render() {
    const { match, date, rows, allRows } = this.props;
    const { openEdit, openDelete, editData, deleteData } = this.state;
    const { closeEdit, closeDelete, dataFatch } = this;
    const { type } = match.params;
    return (
      <>
        <DialogEditContainer
          open={openEdit}
          onClose={closeEdit}
          data={editData}
          type={type}
        />
        <DialogDeleteComponent
          open={openDelete}
          onClose={closeDelete}
          data={deleteData}
          type={type}
        />
        <DatePickerContainer>
          <Fab
            size="small"
            color="primary"
            aria-label="Add"
            onClick={() =>
              this.setState({
                openEdit: true,
                editData: {},
              })
            }
          >
            <Add />
          </Fab>
        </DatePickerContainer>
        <OverviewContainer
          sales={dataFatch('sales', date, rows)}
          spends={dataFatch('spends', date, rows)}
          allSales={dataFatch('sales', date, allRows)}
          allSpends={dataFatch('spends', date, allRows)}
        />
        <TableComponent
          columns={type === 'sales' ? salesColumns : spendsColumns}
          data={dataFatch(type, date, rows)}
          type={type}
          actions={[
            {
              icon: 'edit',
              tooltip: '편집',
              onClick: (event, rowData) => {
                this.setState({
                  openEdit: true,
                  editData: rowData,
                });
              },
            },
            {
              icon: 'delete',
              tooltip: '삭제',
              onClick: (event, rowData) => {
                this.setState({
                  openDelete: true,
                  deleteData: rowData,
                });
              },
            },
          ]}
        />
      </>
    );
  }
}

const mapStateToProps = state => ({
  date: state.date.date,
  rows: state.table.value,
  allRows: state.admin.value,
  admin: state.users.userInfo.admin,
});

const mapDispatchToProps = dispatch => ({
  AdminActions: bindActionCreators(adminActions, dispatch),
  TableActions: bindActionCreators(tableActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(TableContainer);
