import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL
import Fab from '@material-ui/core/Fab';
import NavigateBefore from '@material-ui/icons/NavigateBefore';
import NavigateNext from '@material-ui/icons/NavigateNext';
import { withStyles } from '@material-ui/core/styles';

const styles = ({ spacing }) => ({
  root: {
    marginBottom: spacing.unit * 2,
  },
  button: {
    marginLeft: spacing.unit * 1,
  },
});

class DatePickerComponent extends PureComponent {
  static propTypes = {
    classes: PropTypes.objectOf(String).isRequired,
    date: PropTypes.string.isRequired,
    increasedate: PropTypes.func.isRequired,
    decreasedate: PropTypes.func.isRequired,
    children: PropTypes.objectOf(Function).isRequired,
  };

  render() {
    const { classes, date, increasedate, decreasedate, children } = this.props;
    return (
      <div className={classes.root}>
        {children}
        <Fab
          className={classes.button}
          size="small"
          aria-label="Add"
          onClick={() => decreasedate()}
        >
          <NavigateBefore />
        </Fab>
        <Fab
          variant="extended"
          className={classes.button}
          size="medium"
          aria-label="Add"
        >
          {date}
        </Fab>
        <Fab
          className={classes.button}
          size="small"
          aria-label="Add"
          onClick={() => increasedate()}
        >
          <NavigateNext />
        </Fab>
      </div>
    );
  }
}

export default withStyles(styles)(DatePickerComponent);
