import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as dateActions from '../store/modules/date';

// COMPONENT
import DatePickerComponent from '../components/DatePickerComponent';

class DatePickerContainer extends PureComponent {
  static propTypes = {
    date: PropTypes.objectOf(String).isRequired,
    DateActions: PropTypes.objectOf(Function).isRequired,
    children: PropTypes.objectOf(Function).isRequired,
  };

  render() {
    const { date, DateActions, children } = this.props;
    return (
      <DatePickerComponent
        date={date.date}
        increasedate={DateActions.increaseDate}
        decreasedate={DateActions.decreaseDate}
      >
        {children}
      </DatePickerComponent>
    );
  }
}

const mapStateToProps = state => ({
  date: state.date,
});

const mapDispatchToProps = dispatch => ({
  DateActions: bindActionCreators(dateActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DatePickerContainer);
