import { createAction, handleActions } from 'redux-actions';

import moment from 'moment';

const INCREASE_DATE = 'date/INCREASE_DATE';
const DECREASE_DATE = 'date/DECREASE_DATE';

export const increaseDate = createAction(INCREASE_DATE);
export const decreaseDate = createAction(DECREASE_DATE);

const initialState = {
  date: moment().format('YYYY/MM'),
};

export default handleActions(
  {
    [INCREASE_DATE]: ({ date }) => ({
      date: moment(date, 'YYYY/MM')
        .add(1, 'M')
        .format('YYYY/MM'),
    }),
    [DECREASE_DATE]: ({ date }) => ({
      date: moment(date, 'YYYY/MM')
        .subtract(1, 'M')
        .format('YYYY/MM'),
    }),
  },
  initialState,
);
