/* eslint-disable no-underscore-dangle */
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as tableActions from '../store/modules/table';

// COMPONENT
import DialogSaleEditComponent from '../components/DialogSaleEditComponent';
import DialogSpendEditComponent from '../components/DialogSpendEditComponent';

class DialogEditContainer extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    type: PropTypes.string.isRequired,
    date: PropTypes.objectOf(String).isRequired,
    TableActions: PropTypes.objectOf(Function).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      data: {},
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.data._id !== prevState.data._id) {
      return { data: nextProps.data };
    }
    return null;
  }

  handleChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.value,
      },
    });
  };

  checkChange = name => event => {
    const { data } = this.state;
    this.setState({
      data: {
        ...data,
        [name]: event.target.checked,
      },
    });
  };

  createSales = () => {
    const { TableActions, onClose } = this.props;
    const { data } = this.state;
    TableActions.createRow('/api/sales', data);
    onClose();
  };

  editSales = () => {
    const { TableActions, onClose } = this.props;
    const { data } = this.state;
    TableActions.editRow('/api/sales', data);
    onClose();
  };

  createSpends = () => {
    const { TableActions, onClose } = this.props;
    const { data } = this.state;
    TableActions.createRow('/api/spends', data);
    onClose();
  };

  editSpends = () => {
    const { TableActions, onClose } = this.props;
    const { data } = this.state;
    TableActions.editRow('/api/spends', data);
    onClose();
  };

  render() {
    const { open, onClose, type } = this.props;
    const { data } = this.state;
    const {
      handleChange,
      checkChange,
      createSales,
      editSales,
      createSpends,
      editSpends,
    } = this;
    return (
      <div>
        {type === 'sales' ? (
          <DialogSaleEditComponent
            open={open}
            onClose={onClose}
            data={data}
            handleChange={handleChange}
            checkChange={checkChange}
            createSales={createSales}
            editSales={editSales}
          />
        ) : (
          <DialogSpendEditComponent
            open={open}
            onClose={onClose}
            data={data}
            handleChange={handleChange}
            createSpends={createSpends}
            editSpends={editSpends}
          />
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  date: state.date,
});

const mapDispatchToProps = dispatch => ({
  TableActions: bindActionCreators(tableActions, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(DialogEditContainer);
