import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL
import { withStyles } from '@material-ui/core/styles';

// MATERIAL TABLE
import MaterialTable from 'material-table';

const styles = ({ spacing }) => ({
  table: {
    marginTop: spacing.unit * 2,
  },
});

class TableComponent extends PureComponent {
  static propTypes = {
    columns: PropTypes.arrayOf(String).isRequired,
    data: PropTypes.arrayOf(String).isRequired,
    type: PropTypes.string.isRequired,
    actions: PropTypes.arrayOf(String).isRequired,
    classes: PropTypes.objectOf(String).isRequired,
  };

  // TODO: 밖으로 빼야함
  tableTitle = type => {
    switch (type) {
      case 'sales':
        return '판매 상품';
      case 'spends':
        return '추가 지출';
      case 'admin':
        return '유저 관리';
      default:
        return null;
    }
  };

  render() {
    const { columns, data, type, actions, classes } = this.props;
    const { tableTitle } = this;
    return (
      <div className={classes.table}>
        <MaterialTable
          columns={columns}
          data={data}
          title={tableTitle(type)}
          actions={actions}
          options={{
            pageSize: 10,
            pageSizeOptions: [10, 30, 50],
            exportButton: true,
          }}
          localization={{
            pagination: {
              labelDisplayedRows: '{from}-{to} / {count}',
              labelRowsPerPage: '표시 개수',
              firstAriaLabel: '처음으로',
              firstTooltip: '처음으로',
              previousAriaLabel: '이전',
              previousTooltip: '이전',
              nextAriaLabel: '다음',
              nextTooltip: '다음',
              lastAriaLabel: '마지막',
              lastTooltip: '마지막으로',
            },
            toolbar: {
              nRowsSelected: '{0} 개가 선택 되었습니다',
              showColumnsTitle: '컬럼 보기',
              showColumnsAriaLabel: '컬럼 보기',
              exportTitle: '내보내기',
              exportAriaLabel: '내보내기',
              exportName: '.csv',
              searchTooltip: '검색',
            },
            header: {
              actions: '',
            },
            body: {
              emptyDataSourceMessage: '데이터가 존재하지 않습니다',
              filterRow: {
                filterTooltip: '필터',
              },
            },
          }}
        />
      </div>
    );
  }
}

export default withStyles(styles)(TableComponent);
