import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import { withStyles } from '@material-ui/core/styles';
import logoPng from './logo.png';

const styles = ({ breakpoints, spacing }) => ({
  card: {
    marginLeft: spacing.unit * 3,
    marginRight: spacing.unit * 3,
    marginTop: spacing.unit * 5,
    textAlign: 'center',
    width: 'auto',
    [breakpoints.up(400 + spacing.unit * 3 * 2)]: {
      marginLeft: 'auto',
      marginRight: 'auto',
      width: 400,
    },
  },
  logo: {
    marginTop: spacing.unit * 3,
    width: '100%',
  },
  submit: {
    marginTop: spacing.unit * 3,
  },
});

class LoginComponent extends PureComponent {
  static propTypes = {
    classes: PropTypes.objectOf(String).isRequired,
  };

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.card}>
        <Card>
          <CardContent>
            <img className={classes.logo} src={logoPng} alt="로고" />
            <form action="/api/users/login" method="post">
              <FormControl margin="dense" fullWidth>
                <InputLabel htmlFor="ID">아이디</InputLabel>
                <Input name="userID" id="ID" autoFocus />
              </FormControl>
              <FormControl margin="dense" fullWidth>
                <InputLabel htmlFor="Password">패스워드</InputLabel>
                <Input name="userPW" type="password" id="Password" />
              </FormControl>
              <Button
                className={classes.submit}
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
              >
                로그인
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
    );
  }
}

export default withStyles(styles)(LoginComponent);
