import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL-UI
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContentText from '@material-ui/core/DialogContentText';

// REDUX
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

// ACTION
import * as adminActions from '../store/modules/admin';
import * as tableActions from '../store/modules/table';

class DialogDeleteComponent extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    type: PropTypes.string.isRequired,
    data: PropTypes.objectOf(String).isRequired,
    // ACTION
    AdminActions: PropTypes.objectOf(Function).isRequired,
    TableActions: PropTypes.objectOf(Function).isRequired,
  };

  deleteRow = (type, data) => {
    const { TableActions, onClose } = this.props;
    TableActions.deleteRow(`/api/${type}`, data);
    onClose();
  };

  deleteTitle = type => {
    switch (type) {
      case 'sales':
        return '판매 상품 삭제';
      case 'spends':
        return '추가 지출 삭제';
      case 'admin':
        return '유저 삭제';
      default:
        return null;
    }
  };

  render() {
    const { open, onClose, type, data, AdminActions } = this.props;
    const { deleteTitle, deleteRow } = this;
    return (
      <>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">{deleteTitle(type)}</DialogTitle>
          <DialogContent>
            <DialogContentText>정말로 삭제 하시겠습니까?</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={
                type === 'admin'
                  ? AdminActions.deleteUsers
                  : () => deleteRow(type, data)
              }
            >
              확인
            </Button>
            <Button onClick={onClose}>취소</Button>
          </DialogActions>
        </Dialog>
      </>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  TableActions: bindActionCreators(tableActions, dispatch),
  AdminActions: bindActionCreators(adminActions, dispatch),
});

export default connect(
  null,
  mapDispatchToProps,
)(DialogDeleteComponent);
