/* eslint-disable no-underscore-dangle */
/* eslint-disable no-param-reassign */
import { createAction, handleActions } from 'redux-actions';
import { applyPenders } from 'redux-pender';
import axios from 'axios';
import produce from 'immer';

const CREATE_ROW = 'table/CREATE_ROW';
const FATCH_ROWS = 'table/FATCH_ROWS';
const EDIT_ROW = 'table/EDIT_ROW';
const DELETE_ROW = 'table/DELETE_ROW';

const postRowCreate = (apiUrl, data) => {
  return axios.post(`${apiUrl}/create`, data);
};

const getRowsFatch = apiUrl => {
  return axios.get(`${apiUrl}/fatch`);
};

const postRowEdit = (apiUrl, data) => {
  return axios.post(`${apiUrl}/edit`, data);
};

const postRowDelete = (apiUrl, data) => {
  return axios.post(`${apiUrl}/delete`, data);
};

export const createRow = createAction(CREATE_ROW, postRowCreate);
export const fatchRows = createAction(FATCH_ROWS, getRowsFatch);
export const editRow = createAction(EDIT_ROW, postRowEdit);
export const deleteRow = createAction(DELETE_ROW, postRowDelete);

const initialState = {
  value: {
    sales: [],
    spends: [],
  },
};

const reducer = handleActions({}, initialState);

export default applyPenders(reducer, [
  {
    type: CREATE_ROW,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        const { data } = action.payload;
        const { type } = data;
        draft.value[type].push(data[type]);
      });
    },
  },
  {
    type: FATCH_ROWS,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        const { data } = action.payload;
        const { type } = data;
        draft.value[type] = data[type];
      });
    },
  },
  {
    type: EDIT_ROW,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        const { data } = action.payload;
        const { type } = data;
        draft.value[type] = draft.value[type].map(value =>
          value._id === data[type]._id ? data[type] : value,
        );
      });
    },
  },
  {
    type: DELETE_ROW,
    onSuccess: (state, action) => {
      return produce(state, draft => {
        const { data } = action.payload;
        const { type } = data;
        draft.value[type] = draft.value[type].filter(
          value => value._id !== data[type]._id,
        );
      });
    },
  },
]);
