import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL-UI
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';

export default class DialogSaleEditComponent extends PureComponent {
  static propTypes = {
    open: PropTypes.bool.isRequired,
    onClose: PropTypes.func.isRequired,
    data: PropTypes.objectOf(String).isRequired,
    handleChange: PropTypes.func.isRequired,
    createSpends: PropTypes.func.isRequired,
    editSpends: PropTypes.func.isRequired,
  };

  render() {
    const {
      open,
      onClose,
      data,
      handleChange,
      createSpends,
      editSpends,
    } = this.props;
    return (
      <div>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">
            {data.goods ? '추가 지출 편집' : '추가 지출 추가'}
          </DialogTitle>
          <DialogContent>
            <TextField
              margin="dense"
              id="goods"
              label="지출 상품"
              onChange={handleChange('goods')}
              defaultValue={data.goods ? data.goods : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="name"
              label="지출 내용"
              onChange={handleChange('name')}
              defaultValue={data.name ? data.name : null}
              type="text"
              fullWidth
              variant="outlined"
            />
            <TextField
              margin="dense"
              id="price"
              label="지출 금액"
              onChange={handleChange('price')}
              defaultValue={data.price ? data.price : null}
              inputProps={{ min: 0, step: 1000 }}
              type="number"
              fullWidth
              variant="outlined"
            />
          </DialogContent>
          <DialogActions>
            <Button
              // eslint-disable-next-line no-underscore-dangle
              onClick={data._id ? () => editSpends() : () => createSpends()}
            >
              확인
            </Button>
            <Button onClick={onClose}>취소</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
