import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

// MATERIAL
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

const styles = ({ spacing }) => ({
  text: {
    marginTop: spacing.unit * 3,
  },
});

class OverviewComponent extends PureComponent {
  static propTypes = {
    title: PropTypes.string.isRequired,
    // eslint-disable-next-line react/forbid-prop-types
    children: PropTypes.any.isRequired,
    classes: PropTypes.objectOf(String).isRequired,
  };

  render() {
    const { title, children, classes } = this.props;
    return (
      <Grid item xs={12} md={3}>
        <Card className={classes.card}>
          <CardContent>
            <Typography color="textSecondary" gutterBottom>
              {title}
            </Typography>
            <Typography className={classes.text} variant="h4" component="h2">
              {children}
            </Typography>
          </CardContent>
        </Card>
      </Grid>
    );
  }
}

export default withStyles(styles)(OverviewComponent);
